      <div v-if="historyOfRatings.length > 0" class="history m-b-5">
        <h1 class="text-xl font-extrabold">History</h1>
        <table class="history-table">
          <tr v-for="(rating, index) in historyOfRatings" :key="rating.time">
            <td>{{ formatDate(rating.time) }}</td>
            <td>{{ rating.rating }}</td>
            <td>Â±{{ rating.deviation }}</td>
            <td>
              <button @click="deleteHistoryEntry(index)">âœ–</button>
            </td>
          </tr>
        </table>
      </div>
    </div>
    <div class="explanation info_block my-10">
      <div
        class="explanation-block"
        @click="isGoalCollapsed = !isGoalCollapsed"
      >
        <h1 class="text-xl font-extrabold mb-2">
          Goal of the Chess Puzzle Page
          <span v-if="isGoalCollapsed">â–¼</span>
          <span v-else>â–²</span>
        </h1>
        <div v-if="!isGoalCollapsed">
          <p>
            The Chess Puzzle page is designed to measure and track cognitive
            sharpness and brain energy through the challenge of solving chess
            puzzles. It is a tool for users to assess their mental acuity at
            different times of the day.
          </p>

          <h2 class="text-xl font-extrabold my-2">Rules and Features</h2>
          <ul>
            <li>
              <strong>Chess Puzzle Solving:</strong> Solve chess puzzles within
              a set time limit to test your cognitive skills.
            </li>
            <li>
              <strong>Rating System:</strong> Each puzzle has a difficulty
