            </li>
            <li>
              <strong>User Performance Tracking:</strong> Track your rating
              changes and rating deviation to monitor your progress over time.
            </li>
            <li>
              <strong>History:</strong> A historical record of your ratings is
              maintained to help you see trends in your performance.
            </li>
            <li>
              <strong>Adaptive Difficulty:</strong> Puzzles are matched to your
              rating level to provide a consistent challenge.
            </li>
            <li>
              <strong>Local Storage:</strong> Your rating history is stored
              locally to maintain progress across sessions.
            </li>
            <li>
              <strong>Glicko-2 Rating System:</strong> The user's rating is
              updated using the Glicko-2 system, reflecting changes in skill
              level after each puzzle.
