<script setup>
import { TheChessboard } from "vue3-chessboard";
import "vue3-chessboard/style.css";
import { ref, onMounted } from "vue";
import { Glicko2 } from "glicko2";

const apiUrl = "https://chess.pomodorr.io/get_puzzle";

const puzzle = ref(null);
const historyOfRatings = ref([]);
const showCheckmark = ref(false);
const boardConfig = {};
const isGoalCollapsed = ref(true);

const timeLeft = ref(10);
let countdownInterval = null;

let lastPuzzleSolved = false;

let boardAPI = null;
let movesArray = [];
let sideToPlay = "w";

// Create a Glicko-2 player for the user
let userRating = ref(null);
let userRatingDeviation = ref(null); // You may want to initialize this appropriately
// let lastFailBecauseOfTime = false;

let glicko = null;
let userPlayer = null;

const initBoard = (FEN, moves) => {
  boardAPI.setPosition(FEN);

  const lastMoveSide = FEN.split(" ")[1];
  sideToPlay = lastMoveSide === "w" ? "b" : "w";
  movesArray = moves.split(" ");

  let curr_board_orientation = boardAPI.board.state.orientation;
  curr_board_orientation = curr_board_orientation === "white" ? "w" : "b";

  if (sideToPlay !== curr_board_orientation) {
    boardAPI.toggleOrientation();
  }
};

const fetchPuzzle = async () => {
  try {
    const response = await fetch(`${apiUrl}?rating=${userRating.value}`);
    const data = await response.json();
    puzzle.value = data;
    // Initialize the board with the new puzzle

    initBoard(data.FEN, data.Moves);

    setTimeout(() => {
      if (boardAPI !== null) {
        boardAPI.move(movesArray[0]);
      }
    }, 300);
    resetTimer();
  } catch (error) {
    console.error("Error fetching puzzle:", error);
  }
};

const radius = 26; // The radius of the circle
const circumference = 2 * Math.PI * radius; // Calculate the circumference

// Create a method to start the countdown
const startCountdown = () => {
  timeLeft.value = 10; // Reset the countdown to 10 seconds
  countdownInterval = setInterval(() => {
    if (timeLeft.value > 0) {
      timeLeft.value -= 0.1;

      const circle = document.querySelector(".progress-ring__circle");
      if (circle) {
        const offset = circumference - (timeLeft.value / 10) * circumference;
        circle.style.strokeDashoffset = offset.toString();
        circle.style.strokeDasharray = circumference.toString();
      }
    } else {
      clearInterval(countdownInterval);
      if (lastPuzzleSolved) {
        updateUserRating(false);
      }
      fetchPuzzle();
    }
  }, 100);
};

// Clear the interval when the puzzle is reset
const resetTimer = () => {
  clearInterval(countdownInterval);
  startCountdown();
};

const boardCreated = (api) => {
  boardAPI = api;
  fetchPuzzle();
};

const onMove = (move) => {
  if (boardAPI.game.turn() === sideToPlay) {
    return;
  }
  // console.log(boardAPI);
  let lastMoveIx = boardAPI.game.history().length - 1;

  // check players move
  if (move.lan === movesArray[lastMoveIx]) {
    console.log("correct move");
    // make computer move
    if (lastMoveIx + 1 >= movesArray.length) {
      console.log("Success!");
      showCheckmarkTimeout();
      updateUserRating(true);
      fetchPuzzle();
      return;
    }
    setTimeout(() => {
      boardAPI.move(movesArray[lastMoveIx + 1]);
    }, 300);
    // reset timerToNewPuzzle to 10 sec
    resetTimer();
  } else {
    console.log("wrong move");
    setTimeout(() => {
      boardAPI.undoLastMove();
    }, 300);
    updateUserRating(false);
    fetchPuzzle();
  }
};

// Function to update user rating
const updateUserRating = (puzzleSolved) => {
  // Create a Glicko-2 player for the puzzle
  const puzzlePlayer = glicko.makePlayer(
    puzzle.value.Rating,
    puzzle.value.RatingDeviation
  );

  // Set the result of the match (1 = win, 0.5 = draw, 0 = loss)
  const result = puzzleSolved ? 1 : 0;

  // Update ratings
  glicko.updateRatings([[userPlayer, puzzlePlayer, result]]);

  // Update user's displayed rating
  userRating.value = Math.round(userPlayer.getRating());
  userRatingDeviation.value = Math.round(userPlayer.getRd());
  saveUserRating();

  lastPuzzleSolved = puzzleSolved;
};

onMounted(() => {
  loadRatingsHistory();
  initRatings();
  fetchPuzzle();
});

const initRatings = () => {
  let userVolatility = 0.06;

  glicko = new Glicko2({
    tau: 0.5,
    rating: 1500,
    rd: 200,
    vol: userVolatility,
  });

  // try load user rating from localStorage
  const history = JSON.parse(localStorage.getItem("history")) || [];
  if (history.length > 0) {
    const lastRating = history[history.length - 1];
    userRating.value = lastRating.rating;
    userRatingDeviation.value = 200; // set high deviation to allow fast rating changes
  } else {
    userRating.value = 500;
    userRatingDeviation.value = 1000;
  }

  userPlayer = glicko.makePlayer(
    userRating.value,
    userRatingDeviation.value,
    userVolatility
  );
};

// save User rating, deviation and current time as point in measurement history to localStorage
const saveUserRating = () => {
  const FIVE_MINUTES = 5 * 60;
  const data = {
    rating: Math.round(userPlayer.getRating()),
    deviation: Math.round(userPlayer.getRd()),
    time: Math.round(Date.now() / 1000),
  };

  let history = JSON.parse(localStorage.getItem("history")) || [];

  if (
    history.length === 0 ||
    data.time - history[history.length - 1].time > FIVE_MINUTES
  ) {
    history.push(data);
  } else {
    history[history.length - 1] = data;
  }

  localStorage.setItem("history", JSON.stringify(history));
  loadRatingsHistory();
};

const formatDate = (timestamp) => {
  const date = new Date(timestamp * 1000);
  const year = date.getFullYear();
  const month = (date.getMonth() + 1).toString().padStart(2, "0");
  const day = date.getDate().toString().padStart(2, "0");
  const hours = date.getHours().toString().padStart(2, "0");
  const minutes = date.getMinutes().toString().padStart(2, "0");
  return `${year}-${month}-${day} ${hours}:${minutes}`;
};

const deleteHistoryEntry = (index) => {
  // Remove the entry from the array
  historyOfRatings.value.splice(index, 1);
  // Update localStorage with the new history
  localStorage.setItem(
    "history",
    JSON.stringify(historyOfRatings.value.reverse())
  );
};

const loadRatingsHistory = () => {
  let history = JSON.parse(localStorage.getItem("history")) || [];
  historyOfRatings.value = history.sort((a, b) => b.time - a.time);
};

const showCheckmarkTimeout = () => {
  showCheckmark.value = true;
  setTimeout(() => {
    showCheckmark.value = false;
  }, 1000);
};
</script>



